.wrapper {
  display: -ms-flexbox;
  display: flex;
  height: 100vh;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color: white;
}

.content-wrapper {
  height: 90vh;
  overflow: hidden;
}

.content{
  width: auto;
  height: 100%;
}

/* links */

.links {
  font-size: 18px;
  -ms-flex-positive: 0.35;
      flex-grow: 0.35;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.links a {
  margin-right: 50px;
  color: #203c40;
}

.links a:link,
.links a:visited {
	display: inline-block;
	text-decoration: none;
  border-bottom: 4px solid transparent;
	-webkit-transition: border-bottom 0.2s;
	-o-transition: border-bottom 0.2s;
	transition: border-bottom 0.2s;
}

.links a:hover,
.links a:active {
  border-bottom: 4px solid #203c40;
}

